import {
  CLICK_HOME,
  CLICK_SERVICES,
  CLICK_RESEARCH,
  CLICK_CONTACT
} from '../actions'

interface SectionState {
  homeCurrent: string;
  servicesCurrent: string;
  researchCurrent: string;
  contactCurrent: string
}

type Action = {
  type: string;
  payload: 'home' | 'services' | 'research' | 'contact';
};

const initialState: SectionState = {
  homeCurrent: 'current',
  servicesCurrent: 'invisible',
  researchCurrent: 'invisible',
  contactCurrent: 'invisible'
}

const reducer = (
  state: SectionState = initialState,
  action: Action
) => {
  switch (action.type) {
    case CLICK_HOME:
      return {
        ...initialState
      }
    case CLICK_SERVICES:
      return {
        ...initialState,
        homeCurrent: 'invisible',
        servicesCurrent: 'current'
      }
    case CLICK_RESEARCH:
      return {
        ...initialState,
        homeCurrent: 'invisible',
        researchCurrent: 'current'
      }
    case CLICK_CONTACT:
      return {
        ...initialState,
        homeCurrent: 'invisible',
        contactCurrent: 'current'
      }
    default:
      return initialState;
  }
};

export default reducer;