export const CLICK_HOME = 'CLICK_HOME';
export const CLICK_SERVICES = 'CLICK_SERVICES';
export const CLICK_RESEARCH = 'CLICK_RESEARCH';
export const CLICK_CONTACT = 'CLICK_CONTACT';

// action creator
export function clickHome() {
  return {
    type: CLICK_HOME,
    payload: 'home'
  }
}

export function clickServices() {
  return {
    type: CLICK_SERVICES,
    payload: 'services'
  }
}

export function clickResearch() {
  return {
    type: CLICK_RESEARCH,
    payload: 'research'
  }
}

export function clickContact() {
  return {
    type: CLICK_CONTACT,
    payload: 'contact'
  }
}
