import React from "react";
import { useSelector } from 'react-redux';

import { RootState } from '../store';
import '../css/main.css';
import Header from './Header';


const Research = () => {
  const { researchCurrent } = useSelector((state: RootState) => state.sections);
  return (
    <div>
      <Header />
      <section id="research" className={`page ${researchCurrent === "current" ? "current" : "invisible"}`}>
        <div>
          research section
        </div>
      </section>
    </div>
  )
}

export default Research;