import React from "react";
import { useSelector } from 'react-redux';

import { RootState } from '../../store';
import '../../css/main.css';
import MethodsTable from './MethodsTable';
import Header from '../Header';

const Services = () => {
  const { servicesCurrent } = useSelector((state: RootState) => state.sections);
  return (
    <div>
      <Header />
      <section id="services" className={`page ${servicesCurrent === "current" ? "current" : "invisible"}`}>
        <MethodsTable />
      </section>
    </div>
  )
}

export default Services;