import React from "react";
import { useDispatch, useSelector } from 'react-redux';
import logo from '../images/logo.jpg';
import '../css/main.css';
import {
  store,
  RootState,
  AppDispatch,
  clickHome,
  clickServices,
  clickResearch,
  clickContact
} from '../store';

// interface HeaderState {
//   current: string;
// }

const Header = () => {
  const { homeCurrent, servicesCurrent, researchCurrent, contactCurrent } = useSelector((state: RootState) => state.sections);
  const dispatch = useDispatch();

  return (
    <header id="header-home">
      <div className="container">
        <nav id="main-nav">
          <img src={logo} alt="" />
          <ul>
            <li>
              <a
                href="#home"
                className={homeCurrent === "invisible" ? "regular" : "current"}
                onClick={() => dispatch(clickHome())}
              >
                Home
              </a>
            </li>
            <li>
              <a
                href="#services"
                className={servicesCurrent === "invisible" ? "regular" : "current"}
                onClick={() => dispatch(clickServices())}
              >
                Services
              </a>
            </li>
            <li>
              <a
                href="#research"
                className={researchCurrent === "invisible" ? "regular" : "current"}
                onClick={() => dispatch(clickResearch())}
              >
                Research
              </a>
            </li>
            <li>
              <a
                href="#contact"
                className={contactCurrent === "invisible" ? "regular" : "current"}
                onClick={() => dispatch(clickContact())}
              >
                CONTACT
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  )
}

export default Header;

